"use client"
// ...existing code...
import { useState } from "react"
import Link from "next/link"
import { QrScanner } from "@/components/qr-scanner"
import { Button } from "@/components/ui/button"
import { CreditCard, ClipboardList, Users, ArrowUpRight, ArrowDownLeft, LogOut, UserCircle } from "lucide-react"

type Payment = {
  method: string
  amount: number
  paid_at?: string
  transaction_number?: string
  payment_date?: string
}
type Booking = {
  booking_number: string
  customer_name: string
  mobile_number?: string
  passport_number?: string
  trip: string
  slot_date_time?: string
  status: string
  payment?: Payment
  checked_by?: string
  checked_at?: string
}

export default function OnlineBookingPage() {
  const [bookingNumber, setBookingNumber] = useState("")
  const [booking, setBooking] = useState<Booking | null>(null)
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)

  async function fetchBooking(number: string) {
    if (!number) return
    setLoading(true)
    setError(null)
    setBooking(null)
    try {
      const res = await fetch(`/api/mock-booking/${encodeURIComponent(number)}`)
      if (!res.ok) throw new Error(await res.text())
      const json = await res.json()
      setBooking(json)
    } catch (err: any) {
      setError(err?.message ?? "Failed to fetch booking")
    } finally {
      setLoading(false)
    }
  }

  async function markChecked() {
    if (!booking) return
    setLoading(true)
    setError(null)
    try {
      const res = await fetch(`/api/mock-booking/${encodeURIComponent(booking.booking_number)}/check`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ operator: "operator-ui" }),
      })
      if (!res.ok) throw new Error(await res.text())
      const updated = await res.json()
      setBooking(updated)
    } catch (err: any) {
      setError(err?.message ?? "Failed to update")
    } finally {
      setLoading(false)
    }
  }

  function handleScan(data: string | null) {
    if (!data) return
    try {
      const parsed = JSON.parse(data)
      const bn = parsed.booking_number ?? parsed.reference ?? parsed.id
      if (bn) {
        setBookingNumber(String(bn))
        fetchBooking(String(bn))
        return
      }
    } catch {
      // not JSON
    }
    setBookingNumber(data)
    fetchBooking(data)
  }

  return (
    <main className="min-h-screen bg-slate-900 p-6">
      {/* Navbar */}
 <nav className="bg-gradient-to-r from-slate-900 via-slate-800 to-slate-900 border-b border-slate-700/50 backdrop-blur-lg shadow-lg sticky top-0 z-40">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="h-16 flex items-center justify-between">
            <div className="flex items-center gap-3 group">
              <div className="relative w-11 h-11 bg-gradient-to-br from-emerald-500 to-teal-500 rounded-xl flex items-center justify-center shadow-lg group-hover:shadow-emerald-500/50 transition-all duration-300 group-hover:scale-105">
                <div className="absolute inset-0 bg-white/20 rounded-xl blur-sm"></div>
                <span className="relative text-white font-bold text-lg tracking-tight">AS</span>
              </div>
              <div className="flex flex-col">
                <span className="font-bold text-xl text-white leading-none">Counter Operator</span>
                <span className="text-xs text-emerald-400 font-medium">Service Desk Portal</span>
              </div>
            </div>

            <div className="flex items-center gap-3">
              <div className="hidden sm:flex items-center gap-2 bg-slate-700/50 px-4 py-2 rounded-lg border border-slate-600">
                <UserCircle className="w-5 h-5 text-emerald-400" />
                <span className="text-white font-medium text-sm">Operator #12</span>
              </div>

              {/* Offline Booking */}
              <Link href="/admin/operator/offline-booking">
                <Button className="bg-gradient-to-r from-emerald-600 to-teal-600 hover:from-emerald-700 hover:to-teal-700 text-white gap-2 shadow-lg shadow-emerald-500/30 hover:shadow-xl hover:shadow-emerald-500/40 transition-all duration-300 font-semibold">
                  <CreditCard className="w-4 h-4" />
                  Offline Booking
                </Button>
              </Link>

              {/* Online Booking (new) */}
              <Link href="/admin/operator/online-booking">
                <Button className="bg-gradient-to-r from-indigo-600 to-violet-600 hover:from-indigo-700 hover:to-violet-700 text-white gap-2 shadow-lg shadow-indigo-500/25 hover:shadow-xl transition-all duration-300 font-semibold">
                  <ClipboardList className="w-4 h-4" />
                  Online Booking
                </Button>
              </Link>

              <Button className="border-slate-600 text-slate-300 hover:text-white hover:bg-slate-700/50 gap-2 transition-all duration-300 bg-transparent border">
                <Users className="w-4 h-4" />
                <span className="hidden sm:inline">Queue</span>
              </Button>
              <Button variant="ghost" className="text-slate-300 hover:text-white hover:bg-red-500/10 gap-2 transition-all duration-300 group">
                <LogOut className="w-4 h-4 group-hover:rotate-12 transition-transform" />
              </Button>
            </div>
          </div>
        </div>
      </nav>

      <div className="max-w-3xl mx-auto space-y-6">
        <div className="bg-white rounded-lg p-6">
          <h2 className="text-xl font-bold mb-2">Online Booking â€” Operator</h2>

          <div className="flex gap-2 items-center mb-4">
            <input
              className="flex-1 border rounded px-3 py-2"
              placeholder="Enter booking number"
              value={bookingNumber}
              onChange={(e) => setBookingNumber(e.target.value)}
            />
            <button
              className="px-4 py-2 bg-slate-900 text-white rounded"
              onClick={() => fetchBooking(bookingNumber)}
              disabled={!bookingNumber || loading}
            >
              Search
            </button>
          </div>

          <div className="mb-4">
            <p className="text-sm text-slate-600">Or scan booking QR:</p>
            <div className="mt-2 w-full max-w-md">
              <QrScanner onScan={handleScan} />
            </div>
          </div>

          {loading && <div className="text-sm text-slate-600">Loading...</div>}
          {error && <div className="text-sm text-red-600">{error}</div>}

          {booking && (
            <div className="mt-4 border rounded p-4 bg-gray-50">
              <div className="grid grid-cols-1 sm:grid-cols-2 gap-2">
                <div><strong>Booking:</strong> {booking.booking_number}</div>
                <div><strong>Status:</strong> {booking.status}</div>

                <div><strong>Name:</strong> {booking.customer_name}</div>
                <div><strong>Mobile:</strong> {booking.mobile_number ?? "N/A"}</div>

                <div><strong>Passport No.:</strong> {booking.passport_number ?? "N/A"}</div>
                <div><strong>Slot Date/Time:</strong> {booking.slot_date_time ?? "N/A"}</div>

                <div><strong>Payment Method:</strong> {booking.payment?.method ?? "N/A"}</div>
                <div><strong>Amount:</strong> {booking.payment?.amount ?? "N/A"}</div>

                <div><strong>Payment Transaction #:</strong> {booking.payment?.transaction_number ?? "N/A"}</div>
                <div><strong>Payment Date:</strong> {booking.payment?.payment_date ?? booking.payment?.paid_at ?? "N/A"}</div>
              </div>

              <div className="mt-4 flex items-center justify-end gap-2">
                <div className="text-xs text-slate-600 mr-auto">
                  {booking.checked_by && <div>Checked by: {booking.checked_by}</div>}
                  {booking.checked_at && <div>At: {booking.checked_at}</div>}
                </div>

                <button
                  className="px-4 py-2 bg-emerald-600 text-white rounded disabled:opacity-50"
                  onClick={markChecked}
                  disabled={loading || booking.status === "checked"}
                >
                  {booking.status === "checked" ? "Already Checked" : "Checked (Pass to Go)"}
                </button>
              </div>
            </div>
          )}
        </div>
      </div>
    </main>
  )
}
